'use strict';

document.addEventListener('DOMContentLoaded', async () => {
  const passwordForm = document.getElementById('passwordForm');
  const formContainer = document.getElementById('formContainer');
  const initialPasswordHash = 'a794e600d504779d87143f73a48aa0f9553c3c3e2b9acdbb17a4ea4ff4c4f01f';

  // Initialize the IndexedDB database and store the initial password hash
  const initDb = async () => {
      const openRequest = indexedDB.open('passwordDB', 1);

      openRequest.onupgradeneeded = (event) => {
          const db = event.target.result;
          const passwordStore = db.createObjectStore('passwords', { keyPath: 'id' });
          passwordStore.createIndex('hash', 'hash', { unique: true });
          passwordStore.transaction.oncomplete = () => {
              const store = db.transaction('passwords', 'readwrite').objectStore('passwords');
              store.add({ id: 1, hash: initialPasswordHash });
          };
      };

      return new Promise((resolve, reject) => {
          openRequest.onsuccess = () => resolve(openRequest.result);
          openRequest.onerror = () => reject(openRequest.error);
      });
  };

  const db = await initDb();

  // Hash the input password using SHA-3
  const hashPassword = async (password) => {
      const hash = sha3_256(password);
      return hash;
  };

  // Check if the hashed input password matches the stored password hash
  const checkPassword = async (password) => {
      const hashedPassword = await hashPassword(password);

      return new Promise((resolve) => {
          const transaction = db.transaction('passwords', 'readonly');
          const store = transaction.objectStore('passwords');
          const getRequest = store.get(1);

          getRequest.onsuccess = () => {
              const storedHash = getRequest.result.hash;
              resolve(hashedPassword === storedHash);
          };
      });
  };

  // Replace the password form with the event form
  const showEventForm = () => {
      const eventForm = document.createElement('form');
      eventForm.innerHTML = `
          <label for="eventName">Event name:</label>
          <input type="text" id="eventName" name="eventName" required>
          <br>
          <label for="eventDate">Event date:</label>
          <input type="date" id="eventDate" name="eventDate" required>
          <br>
          <label for="eventTime">Event time:</label>
          <input type="time" id="eventTime" name="eventTime" required>
          <br>
          <button type="submit">Create event</button>
      `;
      formContainer.innerHTML = '';
      formContainer.appendChild(eventForm);

      // Handle the event form submission
      eventForm.addEventListener('submit', (event) => {
          event.preventDefault();
          console.log('Event form submitted');
          // Add your event form handling logic here
      });
  };

  // Handle the password form submission
  passwordForm.addEventListener('submit', async (event) => {
      event.preventDefault();
      const password = event.target.password.value;

      if (checkPassword(password)) {
          showEventForm();
      } else {
          alert('Incorrect password');
      }
  });
});
